.header {
  position: fixed;
  background: #009fe3;
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-evenly;
  align-items: center;
  margin: 0 auto;
  z-index: 10;
}
.header__logo {
  width: 186px;
  height: 87px;
}
.header__menu {
}
.header__link {
  font-size: 13px;
  color: #ffffff;
  text-transform: uppercase;
  border-bottom: solid 1px #ffffff;
  text-decoration: none;
  font-family: "futura bookC", sans-serif;
  line-height: 1;
  text-align: center;
  margin: 0 10px;
  padding: 0;
}
.header__tel {
  font-family: "futura demiC", sans-serif;
  font-size: 20px;
  color: #ffffff;
  padding-right: 5px;
  text-decoration: none;
  margin: 0;
  padding: 0;
  line-height: 1;
  text-align: center;
}
