@font-face {
  font-family: "futura press";
  src: url("../fonts/futurapresspress-webfont.eot");
  src: url("../fonts/futurapresspress-webfont.eot@") format("embedded-opentype"),
    url("../fonts/futurapresspress-webfont.woff2") format("woff2"),
    url("../fonts/futurapresspress-webfont.woff") format("woff"),
    url("../fonts/futurapresspress-webfont.ttf") format("truetype"),
    url("../fonts/futurapresspress-webfont.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "futura bookC";
  src: url("../fonts/futurabookc-webfont.eot");
  src: url("../fonts/futurabookc-webfont.eot@") format("embedded-opentype"),
    url("../fonts/futurabookc-webfont.woff2") format("woff2"),
    url("../fonts/futurabookc-webfont.woff") format("woff"),
    url("../fonts/futurabookc-webfont.ttf") format("truetype"),
    url("../fonts/futurabookc-webfont.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "futura demiC";
  src: url("../fonts/futurademic-webfont.eot");
  src: url("../fonts/futurademic-webfont.eot@") format("embedded-opentype"),
    url("../fonts/futurademic-webfont.woff2") format("woff2"),
    url("../fonts/futurademic-webfont.woff") format("woff"),
    url("../fonts/futurademic-webfont.ttf") format("truetype"),
    url("../fonts/futurademic-webfont.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.site {
  min-height: 100%;
  min-width: 1000px;
  overflow: hidden;
  position: relative;
  z-index: 1;
  font-family: "futura bookC", sans-serif;
  background: url("../img/top-bg.jpg") center top no-repeat;
}
.site:after {
  content: "";
  display: block;
  clear: both;
  width: 100%;
  height: 84px;
}
.wrapper {
  width: 960px;
  position: relative;
  margin: 0 auto;
}
h2 {
  font-family: "futura press", sans-serif;
  text-transform: uppercase;
  font-weight: 400;
}
.top-btn {
  position: fixed;
  right: 50px;
  bottom: 20px;
  z-index: 9;
  opacity: 0.5;
}
.top-btn:hover {
  opacity: 1;
}
