.top-content {
  padding-top: 87px;
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 50px;
}
.top-content__box {
  min-width: 960px;
  z-index: 2;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  background: url(../../../img/girl.png) 60px -100px no-repeat;
}
.top-content__title {
  font-family: "futura bookC", sans-serif;
  max-width: 255px;
  font-size: 27px;
  line-height: 1.5;
  padding: 12px 14px 13px;
  text-transform: uppercase;
  background-color: #009fe3;
  color: #fff;
}
.site-form {
  padding: 10px;
  text-align: center;
  box-sizing: border-box;
  width: 328px;
  height: 433px;
  border-radius: 10px;
  position: relative;
  background: rgb(0, 159, 227);
  border: solid 10px #ede8e9;
  border-radius: 20px;
}
.site-form__title {
  font-size: 28px;
  color: #ffffff;
  display: block;
  margin-bottom: 10px;
  text-align: center;
}
.site-form__subtitle {
  max-width: 230px;
  font-size: 16px;
  color: #ffffff;
  margin: 0 auto 27px auto;
  display: block;
}
.site-form__name {
  border: solid 2px #ffffff;
  font-size: 13px;
  width: 248px;
  height: 51px;
  line-height: 52px;
  margin-bottom: 20px;
  padding: 0 0 0 52px;
  border-radius: 5px;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  background: #ffffff url(../../../img/form-name.png) 13px 15px no-repeat;
}
.site-form__tel {
  border: solid 2px #ffffff;
  font-size: 13px;
  width: 248px;
  height: 51px;
  margin-bottom: 20px;
  line-height: 52px;
  padding: 0 0 0 52px;
  border-radius: 5px;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  background: #ffffff url(../../../img/form-tel.png) 16px 17px no-repeat;
}
.site-form__fieldset {
  border: none;
}
.site-form__agreement {
  vertical-align: bottom;
}
.site-form__agreement-link {
  text-decoration: underline !important;
  color: #fff;
}
.site-form__button {
  width: 248px;
  height: 51px;
  border: none;
  outline: none;
  cursor: pointer;
  border-radius: 5px;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  font-family: "futura demiC", sans-serif;
  font-size: 18px;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  background: linear-gradient(
    to bottom,
    rgba(1, 140, 199, 1) 0%,
    rgba(7, 69, 95, 1) 100%
  );
}

.why-we {
  background: #f1f6f6;
  position: relative;
  z-index: 1;
  margin: -70px 0 0 0;
  text-align: center;
}
.why-we:before {
  content: "";
  width: 100%;
  height: 93px;
  position: absolute;
  top: -16px;
  left: 0;
  background: url(../../../img/why-we_top.png) center repeat-x;
}
.why-we:after {
  content: "";
  width: 100%;
  height: 93px;
  position: absolute;
  bottom: -91px;
  left: 0;
  z-index: 1;
  background: url(../../../img/why-we_bottom.png) center repeat-x;
}
.why-we__lists {
  max-width: 960px;
  margin: 0 auto;
  position: relative;
  box-sizing: border-box;
  min-height: 433px;
  list-style: none;
  background: url(../../../img/why-we_arrow.png) 152px 8px no-repeat;
}
.section__title {
  font-size: 35px;
  color: #403f3f;
  display: block;
  text-align: center;
  margin: 0 0 53px 0;
  padding-top: 113px;
  font-family: "futura press", sans-serif;
  text-transform: uppercase;
  font-weight: 400;
}
.why-we__subtitle {
  display: inline-block;
  vertical-align: top;
  margin: 8px 0 5px 0;
  line-height: 16px;
  font-family: "futura demiC", sans-serif;
  font-size: 13px;
  color: #403f3f;
}
.why-we1 {
  position: absolute;
  left: 366px;
  top: 25px;
  text-transform: uppercase;
  width: 226px;
  text-align: center;
}
.why-we2 {
  left: 731px;
  top: 7px;
  position: absolute;
  width: 150px;
  text-align: center;
}
.why-we3 {
  left: 80px;
  top: 7px;
  position: absolute;
  width: 150px;
  text-align: center;
}
.why-we4 {
  left: 138px;
  top: 221px;
  position: absolute;
  width: 150px;
  text-align: center;
}
.why-we5 {
  left: 647px;
  top: 221px;
  width: 200px;
  position: absolute;
  text-align: center;
}
.why-we__img {
  vertical-align: top;
  margin-bottom: 6px;
}
.why-we__text {
  vertical-align: top;
  margin: 8px 0 5px 0;
  line-height: 16px;
  font-family: "futura demiC", sans-serif;
  font-size: 13px;
  color: #403f3f;
}
.why-we__button {
  border-radius: 7px;
  outline: none;
  cursor: pointer;
  font-family: "futura demiC", sans-serif;
  font-size: 25px;
  line-height: 27px;
  text-transform: uppercase;
  z-index: 2;
  color: #ffffff;
  width: 500px;
  height: 60px;
  border: none;
  padding: 0;
  background: rgb(18, 139, 149);
  background: linear-gradient(
    to bottom,
    rgba(11, 144, 200, 1) 0%,
    rgba(1, 98, 140, 1) 100%
  );
}

.calculate {
  padding-top: 50px;
}
.calculate__box {
  max-width: 960px;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
.calculate__link {
  text-decoration: none;
  margin: 30px;
  max-height: 177px;
}
.calculate__img {
  border-radius: 100px;
  width: 150px;
  height: 150px;
}
.calculate__subtitle {
  font-family: "futura bookC", sans-serif;
  color: #403f3f;
  margin: 0 auto;
  padding: 0;
  font-size: 20px;
  max-width: 157px;
  text-align: center;
  font-weight: bold;
  vertical-align: top;
}

.catalog__list {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-evenly;
  align-items: center;
  margin: 0 auto;
}
.catalog__lists {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: space-evenly;
  align-items: center;
  margin-bottom: 30px;
  max-width: 960px;
  margin: 0 auto;
}
.catalog__subtitle {
  font: bold 18px "pt-sans-narrow", sans-serif;
  margin: 0 0 20px 0;
  color: #403f3f;
  display: block;
  text-align: center;
  padding: 0px 70px;
}
.catalog__box {
  min-height: 179px;
  margin: 0 8px;
}
.catalog__img {
  max-width: 130px;
  max-height: 130px;
}
.catalog__text {
  max-width: 130px;
  text-decoration: none;
  padding: 0;
  margin: 0;
  line-height: 1.5;
  text-align: center;
  font: normal 13px sans-serif, Arial, Tahoma;
  color: #403f3f;
}
.catalog__button {
  display: block;
  width: 500px;
  height: 70px;
  clear: both;
  margin: 30px auto 0 auto;
  border-radius: 10px;
  background: #5a99d4;
  background: linear-gradient(
    to bottom,
    rgba(1, 140, 199, 1) 0%,
    rgba(7, 69, 95, 1) 100%
  );
  color: #fff;
  text-decoration: none;
  font: bold 25px pt-sans-narrow, arial;
  line-height: 70px;
  text-align: center;
  border: none;
  outline: none;
  cursor: pointer;
}
.catalog__info {
  display: none;
  border: 1px solid #d2d6d9;
}
.info__img {
  width: 422px;
  height: 300px;
  margin: 10px;
}
.info__param {
}
.info__name {
  text-align: left;
  font-weight: 400;
  font-family: "futura bookC", sans-serif;
  line-height: 1;
  color: #000;
  margin: 0 0 10px 0;
  padding: 0;
  font-size: 20px;
}
.info__span {
  font-weight: 600;
}
.catalog__info_active {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
}
.catalog__select {
  font: bold 18px "pt-sans-narrow", sans-serif;
  margin: 0 0 20px 0;
  color: #403f3f;
  text-align: center;
  border: solid 2px #403f3f;
  border-radius: 5px;
  text-align-last: center;
}
.catalog__type {
  display: none;
}
.catalog__type_active {
  display: block;
}

